import React from 'react'
import {Context} from '../context/Datacontext'
import Card from 'react-bootstrap/Card'
import {Link} from 'react-router-dom'
export const Content = () => {
    const data = React.useContext(Context)
    const [display , setDisplay] = React.useState([])
    console.log(data)
    React.useEffect(() => {
        if(data.data.data === null) {
            fetch(`https://bestgamesback.herokuapp.com/getAll`)
            .then(response => response.json())
            .then(response => {
                data.update({type: 'add', payload: response})
            })
        }
        else {
            let ds = [];
            for(let item of data.data.display) {
                ds.push(
                    <Link to={`/product=${item.id}`}> <Card style={{ width: '18rem' }} className="card">
                        <Card.Img variant="top" src={item.image} />
                        <Card.Body>
                        <Card.Title style={{fontSize: "1rem", textAlign: 'center'}}>{item.name}</Card.Title>
                        </Card.Body>
                    </Card></Link>
                )
            }
            setDisplay(
                <div style={{display: 'flex', justifyContent: 'center', flexWrap: 'wrap'}}>
                    {ds}
                </div>
            )
        }
        
    }, [data])
    return (
        <div id="right">
            {display}
        </div>
    )
}
