import React from 'react'

export const Context = React.createContext()

const initial = {
    data: null,
    display: null,
}

const update = (state , action) => {
    if(action.payload === 'filter') {
        const toAdd = state.data.filter(item => item.type === action.type)
        console.log(action.type)
        return {
            ...state,
            display: toAdd
        }
    }
    
    return {
        ...state,
        data: action.payload.products,
        display: action.payload.products
    }
}

export const Datacontext = (props) => {
    const [state , dispatch] = React.useReducer(update , initial)
    return (
        <Context.Provider value={{data: state , update: dispatch}}>
            {props.children}
        </Context.Provider>
    )
}
