import React from 'react'
import Nav from 'react-bootstrap/Nav'
import {Context} from '../context/Datacontext'
export const Left = () => {
    const data = React.useContext(Context)
    return (
        <div id="left">
            <h3>categories</h3>
            <Nav.Link className="link" onClick={() => {data.update({type: "pc", payload: "filter"})}}>
                pcs
            </Nav.Link>

            <Nav.Link className="link" onClick={() => {data.update({type: "mouse", payload: "filter"})}}>
                souris
            </Nav.Link>

            <Nav.Link className="link" onClick={() => {data.update({type: "keyboard", payload: "filter"})}}>
                claviers
            </Nav.Link>

            <Nav.Link className="link" onClick={() => {data.update({type: "headset", payload: "filter"})}}>
                casques
            </Nav.Link>

            <Nav.Link className="link" onClick={() => {data.update({type: "pad", payload: "filter"})}}>
                tapis
            </Nav.Link>

            <Nav.Link className="link" onClick={() => {data.update({type: "game", payload: "filter"})}}>
                jeux
            </Nav.Link>
        </div>
    )
}
